import './App.css'

import { Observer } from 'mobx-react-lite'
import { SnackbarProvider } from 'notistack'
import React from 'react'

import { ConnectionWarning } from './atoms/ConnectionWarning'
import { ErrorContainer } from './atoms/ErrorContainer'
import Pacman from './atoms/Pacman'
import { useRoot } from './atoms/RootProvider'
import { Routing } from './routes/Routing'

export const App: React.SFC = () => {
  const { user } = useRoot()
  return (
    <ErrorContainer prod={process.env.NODE_ENV === 'production'}>
      <>
        <ConnectionWarning />
        <SnackbarProvider
          maxSnack={3}
          anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
        >
          <Observer>{() => (user.isReady ? <Routing /> : <Pacman />)}</Observer>
        </SnackbarProvider>
      </>
    </ErrorContainer>
  )
}
